@media (min-width: 700px) {
  .Intro p {
    padding: 0px 90px;
  }
}
@media (min-width: 800px) {
  .Intro p {
    padding: 0px 160px;
  }
}

@media (min-width: 1000px) {
  .Intro p {
    padding: 0;
  }
}

@media (min-width: 760px) {
  @media (min-height: 760px) {
    .Intro {
      /* color: burlywood; */
      margin: 70px auto;
    }
    .Intro p {
      /* font-size: 20px !important; */
      padding-bottom: 10px;
    }
  }
}

