.Navbar {
  margin-top: 4vh;
  /* margin-bottom: 4vh; */
}
.Navbar a {
  text-decoration: underline;
  color: white;
}
.Navbar .list-group-item {
  background-color: transparent;
  margin: 0;
  color: white;
}
.Navbar .list-group-item.hover {
  color: #ffffcc;
}

.list-group-item {
  background-color: transparent;
  margin: 0;
  padding: 0;
  border: 0;
}

.front-nav-bar ul {
  font-family: 'Yantramanav', helvetica, sans-serif;
  text-transform: uppercase;
  padding: 0;
}
.front-nav-bar ul {
  font-size: 1em;
  letter-spacing: 0em;
}
.front-nav-bar li {
  -webkit-appearance: none !important;
}

@media (max-height: 319px) {
  .list-group-item {
    /* padding: 0rem; */
  }
}

@media (min-height: 320px) {
  .Navbar {
    margin-bottom: 12vh;
  }
  .list-group-item {
    font-size: 1.3em;
  }
}

@media (min-height: 375px) {
  .list-group-item {
    font-size: 1.8em;
  }
}

@media (max-width: 475px) {
  .list-group {
    display: block;
  }
}

@media (max-width: 1000px) {
  @media (min-height: 1000px) {
    .Navbar .list-group {
      display: block;
    }
    .Navbar .list-group-item {
      margin-top: 20px;
    }
  }
}

