.App {
  /* position: relative; */
  /* background-color: brown; */
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  min-height: 100%;
  width: 100%;
  height: 100%;
  margin: auto;
  /* max-width: 1000px; */
}

@media (min-height: 375px) {
  .App {
    min-height: 375px;
  }
}

#cover {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

#shader {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: rgba(25, 25, 25, 0.5);
}

.bg-img {
  width: 100%;
  height: 100%;
  object-position: 50% 50%;
  object-fit: cover;
}

main {
  /* background-color: burlywood; */
  /* display: table-cell; */
  margin-top: 0;
  margin-bottom: 0;
  /* text-align: center; */
  /* vertical-align: middle; */
  /* max-width: 1000px; */
}

.Modal {
  color: black;
}

footer {
  /* background-color: chartreuse; */
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
}

.Footer img {
  width: 36px;
}

.Footer img.hover {
  width: 44px;
  /* opacity: 1; */
}
.Footer img.unhover {
  opacity: 0.8;
}

Button {
  text-transform: capitalize;
  /* -webkit-appearance: none !important; */
}

.Footer span {
  /* -webkit-appearance: none; */
  -webkit-appearance: none !important;
}

.w200px {
  width: 200px;
}

